<template>
  <div class="edit">
    <h1>Card Info</h1>
    <form class="edit-form" action="#">
      <input type="text" id="title" v-model="editedCard.title" placeholder="title" :class="{invalid: !editedCard.title}">
      <input type="text" id="text" v-model="editedCard.text" placeholder="description" :class="{invalid: !editedCard.text}">
      <div class="buttons">
        <button class="save-btn" type="button" @click="saveCard">Save</button>
        <button class="delete-btn" type="button" @click="confirmOpened = true">Delete</button>
      </div>
    </form>

    <div class="overlay" v-if="confirmOpened">
      <div class="confirm" v-if="confirmOpened">
        <p>Are you sure?</p>
        <div class="confirm-btns">
          <button @click="deleteCard">Yes</button>
          <button @click="confirmOpened = false">No</button>
        </div>
      </div>
    </div>    
  </div>
</template>

<script>
export default {
  props: ['card'],
  data() {
    return {
      editedCard: {},
      confirmOpened: false
    }
  },
  mounted() {
    if (this.card) {
      localStorage.setItem('editedCard', JSON.stringify(this.card))
      this.editedCard = this.card
    } else {
      this.editedCard = JSON.parse(localStorage.editedCard)
    }
  },
  methods: {
    saveCard() {
      if (this.editedCard.title && this.editedCard.text) {
        this.$router.push({ name: 'Home', params: { editedCard: this.editedCard }})
      }      
    },
    deleteCard() {
      this.$router.push({ name: 'Home', params: { deletedCard: this.editedCard }})
    }
  }
}
</script>

