<template>
  <div class="edit">
    <h1>Card Info</h1>
    <form class="edit-form" action="#">
      <input type="text"
             id="title"
             v-model="newCard.title" 
             placeholder="title" 
             maxlength="20"
             autocomplete="off"
             :class="{invalid: !newCard.title}">
      <input type="text"
             id="text" 
             v-model="newCard.text" 
             placeholder="description" 
             autocomplete="off"
             maxlength="125"
             :class="{invalid: !newCard.text}">
      <div class="buttons">
        <button class="save-btn" type="button" @click="createCard">Save</button>
      </div>
    </form>
  </div>
</template>

<script>
export default {
  data() {
    return {
      newCard: {
          id: Date.now(),
          title: 'Title',
          text: 'Description',
          liked: false
      }
    }
  },
  methods: {
      createCard() {
          if (this.newCard.title && this.newCard.text) {
              this.$router.push({ name: 'Home', params: { newCard: this.newCard }})
          }          
      }
  }
}
</script>

<style lang="scss" scoped>
.edit {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
}
.edit-form {
  margin-top: 40px;
  padding: 40px 20px;
  width: 300px;
  background-color: #e2e2e2;
  border: 2px solid #666666;
}

input {
  box-sizing: border-box;
  padding: 10px 5px;
  width: 100%;
  border: 3px solid #999999;
  margin-bottom: 10px;
  outline: none;

  &.invalid {
    border-color: red;
  }
}

.save-btn,
.delete-btn {
  padding: 5px;
  width: 80px;
  cursor: pointer;
  outline: none;
}

.delete-btn {
  background-color: transparent;
  border: none;
  margin-left: 20px;
}

.buttons {
  margin-top: 20px;
}


</style>
